.portfolio {
    height: 100%;
    /* background-color: #f78888; */
    background-color: white;
    padding: 8rem 0;
    text-align: center;
}

.portfolio h1 {
    margin: 0;
    color: #5b5b5b;

    border-bottom: 0.2rem solid#5b5b5b;
    display: inline-block;  
    padding-bottom: 0.5rem;
    margin-bottom: 2rem;
}

.portfolio ul {
    width: 100%;
    margin: 2rem auto;
    padding: 0;   
    text-align: center; 
}
.portfolio li {
    /* width: 40rem; */
    margin: 0 2rem 3rem 2rem;
    color: #4c4c4c;
    -webkit-box-shadow: 0 0 20px #333;
            box-shadow: 0 0 20px #333;
    text-align: center;
    /* height: 53rem; */
    display: inline-block;
    vertical-align: top;

    width: 25rem;
    height: 35rem;

}

.safistation img {
    width: 100%;
    height: 17rem;
}

.safistation p{
    height: 7.5rem;
    font-size: 8pt;
}

.safistation h2 {
    font-size: 14pt;
    height: 5rem;
    margin: 0.5rem 0;
    padding: 1rem 0;
}

.safistation h4 {
    text-align: center;
}


